import React, { useContext, useEffect, useState } from 'react';
import TableForm from '../Components/TableForm';
import InfoTableContainer from './InfoTableContainer';
import ResultForm from '../Components/ResultForm';
import { appContext } from './AppMain';
import { DIVIDE_SLEEVE_COUNT } from '../utils/constants';

const SizeInfo = (props) => {
    // 아래 스테이트들 AppMain Dp context provider 로 옮김
    // const [neckBack, setNeckBack] = useState(0);
    // const [raglanSts, setRaglanSts] = useState(2);
    const [divSleeves, setdivSleeves] = useState(0); // 소매 나눔 수
    // const [totalSts, setTotalSts] = useState(48); // 총 콧 수
    // const [sleevesSts, setSleevesSts] = useState(0); // 소매 코수
    // const [backSts, setBackSts] = useState(0); // 뒷판 코수
    let context = useContext(appContext);

    useEffect(() => {
        let afterSts = context.afterSts;
        let neckBack = context.neckBack;
        // 총 콧수 = 세탁후 코수 * (뒷목 둘레 /10)
        if(neckBack > 0){
            let totalSts = afterSts * (neckBack / 10);
            context.setTotalSts(totalSts);
            context.setTotalSts(totalSts);
        }
            
    }, [context.neckBack, context]);

    useEffect(() => {
        let totalSts = context.totalSts;
        let raglanSts = context.raglanSts;
        if(totalSts > 0){
            let divide = divSleeves ? divSleeves : DIVIDE_SLEEVE_COUNT;
            // 소매 콧수 = 총 콧수 / DIVIDE_SLEEVE_COUNT - raglanSts
            // 반올림
            context.setSleevesSts(Math.round((totalSts / divide)) - raglanSts);
        }

    }, [context.totalSts, divSleeves, context.raglanSts, context]);

    useEffect(() => {
        let totalSts = context.totalSts;
        let sleevesSts = context.sleevesSts;
        let raglanSts = context.raglanSts;
        if(totalSts > 0 && raglanSts > 0 && sleevesSts > 0) {
            let back = totalSts - (sleevesSts * 2) - (raglanSts * 4);
            // 뒷판 코수 = (소매 코수 * 2) - 2(양쪽 앞섶?) - (raglanSts * 4)
            context.setBackSts(back);
        }
    }, [context.totalSts, context.setSleevesSts, context.raglanSts, context]);


    const handleChangeNeckBack = e => {
        context.setNeckBack(e.target.value);
        context.setNeckBack(e.target.value);
    }

    const handleChangeRaglanSt = e => {
        context.setRaglanSts(e.target.value);
    }

    const handleChangeSleeves = e => {
        setdivSleeves(e.target.value);
    }

    const sizeInfo = [
        {
            label: "뒷목 둘레",
            txtInfo: [
                {
                    id: "neckBack",
                    txtLabel: "뒷목 둘레를 입력하세요",
                    type: "number",
                    required: true,
                    onChange: handleChangeNeckBack,
                    endAdornment: "cm",
                }
            ],
        },
        
        {
            label: "래글런코",
            txtInfo: [
                {
                    id: "raglanSt",
                    txtLabel: "래글런코를 입력하세요",
                    type: "number",
                    required: true,
                    onChange: handleChangeRaglanSt,
                    endAdornment: "코",
                },
            ],
        },
        {
            label: "소매 나눔 수",
            txtInfo: [
                {
                    id: "sleeves",
                    txtLabel: "소매 나눔 수를 입력하세요",
                    type: "number",
                    required: true,
                    onChange: handleChangeSleeves,
                    
                },
            ],
        },
    ];

    const resSts = [
        {
            label: "총 콧수",
            results: [
                {
                    id: "totalSt",
                    txtLabel: context.totalSts,
                    onChange: null,
                    endAdornment: "코",
                    variant: "filled",
                }
            ]
        },
        {
            label: "소매 콧수",
            results: [
                {
                    id: "sleevesSts",
                    txtLabel: context.sleevesSts,
                    onChange: null,
                    endAdornment: "코",
                    variant: "filled",
                }
            ]
        },
        {
            label: "뒷판 콧수",
            results: [
                {
                    id: "backSts",
                    txtLabel: context.backSts,
                    onChange: null,
                    endAdornment: "코",
                    variant: "filled",
                }
            ]
        },
    ];
    
    return (
        <React.Fragment>
             <InfoTableContainer>
                 {
                     sizeInfo.map((info, idx) => (
                        <TableForm key={idx} label={info.label} txtInfo={info.txtInfo} />
                     ))
                 }
            </InfoTableContainer>

            <InfoTableContainer>
                {
                    resSts.map((res, idx) => (
                        <ResultForm key={idx} label={res.label} results={res.results} />
                    ))
                }
            </InfoTableContainer>
        </React.Fragment>
        );
}


export default SizeInfo;